body {
  margin: 0;
  padding: 0;

  background-color: #2d2d3e;

  font-size: 16px;
  font-weight: bold;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  color: #f18730;
}

.app-contenedor {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "header header header" "sidebar-left main sidebar-right" "footer footer footer"; }

.toolbox-contenedor {
  grid-area: sidebar-left;
  display: flex;
  flex-direction: column;
  align-items: center; }

.acciones-contenedor {
  display: flex;
  flex-direction: column; }

.toolbox-acciones {
  display: flex;
  flex-direction: column;
  width: 150px;
  height: 100%;
  margin: 4px 0 10px 0;
  background-color: #f1efff; }

.row {
  display: flex;
  flex-direction: row; }

.accion {
  width: 50px;
  height: 50px;
  margin: 8px 13px 8px 12px; }

.accion:hover {
  cursor: pointer; }

.borde-redondeado {
  border-radius: 4px; }

.titulo {
  text-align: start;
  margin-left: 8px; }

.detalle-contenedor {
  grid-area: sidebar-right;
  display: flex;
  flex-direction: column;
  align-items: center; }

.detalle-contenido {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 250px;
  height: 500px;
  margin-top: 4px;
  background-color: #f1efff;
  color: #495163; }

.nombre-maquina {
  margin-top: 16px;
  margin-left: 16px; }

.costo-maquina {
  margin-top: 8px;
  margin-left: 16px; }

.frecuencia-maquina {
  margin-top: 8px;
  margin-left: 16px; }

span {
  color: #bc4062; }

.fabrica-contenedor {
  grid-area: main;
  display: flex;
  justify-content: center; }

.fabrica {
  display: grid;
  border: 1px solid #2d2d3e; }

.celda {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f1efff;
  border: 1px solid #cedae9;
  position: relative; }
  .celda.seleccionada {
    border: 3px solid #e9873b; }

.materia-prima {
  border: solid 1px #e9873b;
  background-color: #e9873b;
  border-radius: 10px;
  height: 20px;
  width: 20px;
  font-size: 12px;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute; }

.norte {
  height: 100%;
  width: 100%;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg); }

.sur {
  height: 100%;
  width: 100%;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg); }

.este {
  height: 100%;
  width: 100%;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg); }

.oeste {
  height: 100%;
  width: 100%;
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg); }

.ganancia-contenedor {
  grid-area: header;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px; }

.ganancia {
  background-color: #485262;
  margin-left: 8px;
  color: white;
  padding: 1px 10px 1px 10px; }

